import React from 'react'
import img from './../icons/d20.png'
import saveImg from './../icons/save.png'
import downloadimg from '../icons/download.png'
import Popup from './subconponents/Popup.jsx'
import { useState } from 'react'
import Form from './subconponents/Form.jsx'
import FormAtt from './subconponents/FormAtt.jsx'
import saveinfo from '../backend/SaveInfo.js'
import DoABarrelRoll from './subconponents/DoABarrelRoll.jsx'
import saveAs from './../backend/fileSaver.js'
import saveLoad from './style/saveLoad.css'

export default () => {
    const [buttonPopupInfo, setButtonPopupInfo] = useState(false);
    const [buttonPopupExpertise, setButtonPopupExpertise] = useState(false);
    const [buttonRoll, setButtonRoll] = useState(false);
    const [buttonSave, setButtonSave] = useState(false);

    var data = localStorage.getItem('ficha_');
    var info = JSON.parse(data);

    if (info.nome_do_personagem && info.nome_do_personagem != '??'){
        document.title = "Ficha - "+info.nome_do_personagem;
    }

    var autSaveAs = () => {
        var save = new Blob([data], {
            type: 'application/json'
        })
        saveAs(save, `${info.nome_do_personagem || 'guia'}.venatores`);
    }
    
    var autLoadAs = () => {
        document.getElementById('load-file').addEventListener('change', (event)=>{
            var reader = new FileReader();
            reader.onload = (file)=>{
                var result = file.srcElement.result;
                localStorage.setItem('ficha_', result);
            }
            var fileBinary = reader.readAsText(event.target.files[0]);
            window.location.reload();
        })
    }
    
    return (
        <>
            <div id="Edit">
                <button className="popup-btn" onClick={()=>setButtonPopupInfo(true)}>
                    Informações
                </button>
                <button className="popup-btn" onClick={()=>setButtonPopupExpertise(true)}>
                    Pericias
                </button>
                <button className="popup-btn" onClick={()=>setButtonSave(true)}>
                    Save
                </button>
                <button className="d20" onClick={()=>setButtonRoll(true)}>
                    <img src={img} /> 
                </button>
            </div>
            <Popup trigger={buttonSave} setTrigger={setButtonSave} tam={[160, 200]}>
                <a href="" className="save-choice" onClick={autSaveAs}>
                    <p>Salvar</p>
                </a>    
                <a href="" type='file' className="save-choice">
                    <label htmlFor="load-file">Carregar</label>
                    <input type="file" id="load-file" onClick={autLoadAs}/>
                </a>
            </Popup>
            <Popup trigger={buttonPopupInfo} setTrigger={setButtonPopupInfo}>
                <button className="save">
                    <img src={saveImg} onClick={()=>{saveinfo('Form.json')}}/>
                </button>
                <h2>Editar Ficha</h2>
                <Form />
            </Popup>
            <Popup trigger={buttonPopupExpertise} setTrigger={setButtonPopupExpertise}>
                <button className="save">
                    <img src={saveImg} onClick={()=>{saveinfo('Attributes.json')}}/>
                </button>
                <h2>Pericias</h2>
                <div className="form">
                    <FormAtt />
                </div>
            </Popup>
            <Popup trigger={buttonRoll} setTrigger={setButtonRoll} tam={[185, 300]}>
                <DoABarrelRoll per={50} complex={false}/>
            </Popup>
        </>
    )
}