import React from 'react'
import Score from './subconponents/Score.jsx'
import ScoreInv from './subconponents/ScoreInv.jsx'

export default () => {
    var data = JSON.parse(localStorage.getItem('ficha_'));

    if (!data.gasta_tudo){
        data['gasta_tudo'] = 0;
        localStorage.setItem('ficha_', JSON.stringify(data));
    }

    return (
        <div id="Points">
            <div id="Score">
                <Score inf_req="VIDA"/>
                <Score inf_req="SAN"/>
                <Score inf_req="MA"/>
                <ScoreInv inf_req="INV"/>
            </div>
        </div> 
    )
}