import React from 'react'
import ammocss from './../style/ammo.css'

export default (item) =>{
    var addOnclick = (i) => {
        var mm = document.getElementsByClassName('heavyWeaponsGuy')[0].value;
        var data = JSON.parse(localStorage.getItem('ficha_'));
        var c = -1;
        for (let gun of data.guns){
            if (gun.name == i.name){
                break;
            }
            c++;
        }
        data.guns[c].ammo = Number(data.guns[c].ammo) + Number(mm);
        localStorage.setItem('ficha_', JSON.stringify(data));
        return window.location.reload();
    }

    var rmOnclick = (i) => {
        var mm = document.getElementsByClassName('heavyWeaponsGuy')[0].value;
        var data = JSON.parse(localStorage.getItem('ficha_'));
        var c = -1;
        for (let gun of data.guns){
            if (gun.name == i.name){
                break;
            }
            c++;
        }
        data.guns[c].ammo = Number(data.guns[c].ammo) - Number(mm);
        localStorage.setItem('ficha_', JSON.stringify(data));
        return window.location.reload();
    }

    return (
        <div className="item-ammo">
            <input className="heavyWeaponsGuy" type="number" placeholder="XX"/>
            <button onClick={addOnclick}>+</button>
            <button onClick={rmOnclick}>-</button>
        </div>
    )
}