import React from 'react'
import listStyle from '../style/listStyle.css'
import ByeItems from './ByeItems.jsx'
import Roll from './Roll.jsx'
import DoABarrelRoll from './DoABarrelRoll.jsx'
import { useState } from 'react'
import Popup from './Popup.jsx'
import img from '../../icons/d20.png'
import redimg from '../../icons/target.png'
import ammoImg from '../../icons/ammo.png'
import popupcss from '../style/popup.css'
import Ammo from './Ammo.jsx'
import dist from './../style/distorcionDBR.css'

export default () => {
    var saved = JSON.parse(localStorage.getItem('ficha_'));
    var data = saved.guns;

    var nonFullName = (name) => {
        var returnName = '';
        for (let i=0; i<=7; i++){
            if (!name[i]){
                break;
            }
            returnName += name[i];
            if (i == 7){
                returnName += '...';
            }
        }
        return returnName;
    }

    if (data != undefined){
        return (
            <ul className="InvList">
                {
                    data.map((item, index)=>{
                        const [buttonRollDice, setButtonRollDice] = useState(false);
                        const [buttonRollPer, setButtonRollPer] = useState(false);
                        const [ammoPop, setAmmoPop] = useState(false);
                        return (
                            <li>
                                <p className="gun-inp">{nonFullName(item.name)}</p>
                                <label className="gun-inp">{item.dice}</label>
                                <label className="gun-inp">{item.heavy}p</label>
                                <button className="ammo-inp" onClick={()=>setAmmoPop(true)}>
                                    <img src={ammoImg} name="ammo"></img>
                                    <label className="gun-inp">{item.ammo}</label>
                                </button>

                                <div className="bttsstuff">
                                    <button className="d20" onClick={()=>{setButtonRollDice(true)}}>
                                        <img src={redimg}/> 
                                    </button>
                                    <button className="d20" onClick={()=>{setButtonRollPer(true)}}>
                                        <img src={img}/> 
                                    </button>
                                </div>
                                <ByeItems index={index} inv="guns"/>
                                <Popup trigger={buttonRollDice} setTrigger={setButtonRollDice} 
                                tam={[235, 300]}>
                                    <DoABarrelRoll per={item.dice} complex={item.dice}/>
                                </Popup>

                                <Popup trigger={buttonRollPer} setTrigger={setButtonRollPer} 
                                tam={[235, 300]}>
                                    <DoABarrelRoll per={saved[(item.type).toLowerCase()]} 
                                    complex={true}/>
                                </Popup>

                                <Popup trigger={ammoPop} setTrigger={setAmmoPop} 
                                tam={[90, 170]}>
                                    <Ammo heavy={item}/>
                                </Popup>
                            </li>
                        )
                    })
                    }
            </ul>
        )
    } return (<></>);
}