import React from 'react'
import { useState } from 'react'
import img from './../../icons/d20.png'
import Popup from './Popup.jsx'
import DoABarrelRoll from './DoABarrelRoll.jsx'

export default (req) => {
    const [buttonRollDice, setButtonRollDice] = useState(false);
    return (
        <div className="detail roll">
            <p>{req.inf_req}:</p>
            <label>{req.local || 'XX'}</label>
            <button className="d20" onClick={()=>{setButtonRollDice(true)}}>
                <img src={img}/> 
            </button>

            <Popup trigger={buttonRollDice} setTrigger={setButtonRollDice} tam={[235, 400]}>
                <DoABarrelRoll per={Number(req.local)} complex={true}/>
            </Popup>
        </div>  
    )
}