import React from 'react'
import img from './../../icons/d20.png'
import css from './../style/distorcionDBR.css'

export default (req) => {
    var random = Math.floor(Math.random() * 100 );

    var rollDice = (per, dice, complex) => {
        var save = complex;
        if (complex == false){ return [false, dice]; }

        var ComplexValues = ['Fracasso', 'Normal', 'Bom', 'Extremo', 'Desastre'];

        if (complex != true){
            var value = complex.split('d');
            value[1] = value[1].split('+');

            value[2] = value[1][1] || 0;
            value[1] = value[1][0];

            var minus = false;
            if (value[2] == 0){
                value[1] = value[1].split('-');
                value[2] = value[1][1] || 0;
                value[1] = value[1][0];
                minus = true;
            }
            
            random = 0;
            for (var i = 1; i <= value[0]; i++){
                random += Math.floor(Math.random() * (value[1]) + 1);
            }

            if (minus){
                random = Number(random) - Number(value[2]);
                return [save, random, true];
            }

            random = Number(random) + Number(value[2]);
            return [save, random, true];
        }
        else {
            return [ComplexValues[ 
                (dice < per) + (dice < per / 2) + (dice < per / 5) + (dice <= 1)
            ], dice];
        }
    }

    var final = rollDice(req.per, random, req.complex);
    var exPer = ()=>{
        if (final[2] == true){
            return 'per-value dice-per';
        } return 'per-value';
    }
    var exPer = exPer();
    return (
        <div className="DoABarrelRoll">
            <img src={img} alt="d20"/>
            <label className="dice-value">{final[1]}</label>
            <label className={exPer} >{final[0]}</label>
        </div>
    )
}
