import React from 'react'
import style from './../style/form.css'
import formdata from './../data/Attributes.json'

export default (quote) => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    return formdata.map((item)=> {
        return (
            <div>
                <p>{item}:</p>
                <input type="number" 
                placeholder={data[item.replaceAll(' ','_').replaceAll('combate_com_', '')] || 'XX'}
                className="input-form">
                </input>
            </div>
        )
    })
}